import List from './components/List.vue'
import Add from '../../components/my-add/add.vue'
export default {
	data() {
		return {
			monthSum: {},
			date: '',
			income: 25,
			pay: 99,
			list: []
		}
	},
	created() {
		this.getNowTime();
	},
	onShow() {  //页面显示重新取数据
		this.getMonthSum();
		this.getNoteData();
	},
	methods: {
		getNowTime() {
			const date = new Date();
			const dateNow = `${date.getFullYear()}-${date.getMonth()+1 < 10 ? `0${date.getMonth()+1}`: date.getMonth()+1}`;
			this.date = dateNow;
		},
		addAccount() {
			uni.navigateTo({
				url: './pages/add_account/add_account'
			});
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		getMonthSum() {
			this.$ajax('/bill/getMonthSum', {
				timeStart: this.date
			}).then(res => {
				if (res.status) {
					this.monthSum = res.data;
				}
			});
		},
		getNoteData() {
			const dt = {
				timeStart: this.date
			};
			this.$ajax('/bill/query', dt).then(res => {
				if (res.status) {
					this.formatData(res.data);
				}
			});
		},
		formatData(data) { //按每日日期格式化数据   [{date: '2020-01-01', list: []},{...}]
			const arr = [];
			data.map(item => {
				let index = arr.findIndex(itm => {
					return itm.date == item.date;
				});
				if (index == -1) { //arr中不存在键，创建并添加
					const list = [];
					list.push(item);
					arr.push({
						date: item.date,
						pay: 0,
						income: 0,
						list
					});
				} else { //arr中存在键，添加进list
					arr[index].list.push(item);
				}
			});
			arr.map(item => {
				item.list.map(itm => {
					if (itm.type == 0) { //支出
						item.pay += itm.price;
					} else { //收入
						item.income += itm.price;
					}
				});
			});
			this.list = arr;
		}
	},
	components: {
		List,
		Add
	},
	watch: {
		date(newVal) {
			this.getMonthSum();
			this.getNoteData();
		}
	}
}
